.Nav-page{
    display: -webkit-flex;
    display: flex;
    position: fixed;
    width: 100%;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    z-index: 900;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    top: 15%;
    padding: 15px 30px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-transition: 0.5s all;
    transition: 0.5s all;
}
.nav-left a{
    color: white;
    font-size: 25px;
    margin-top: auto;
    margin-bottom: auto;
    padding-left: 20px;
}
.nav-right-ul ul{
    margin-top: auto;
    margin-bottom: auto;
}
.home-page .nav-right-ul ul,.nav-right,.nav-left{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.nav-right-ul ul li{
    list-style: none;
}
.nav-right-ul ul li a{
    text-decoration: none;
    padding: 10px;
    cursor: pointer;
    color: white;
}
.nav-right-regist{
    margin-left: 30px;
}
.nav-right-regist button{
    background: transparent;
    color: white;
}
.nav-right-regist a{
    padding: 10px;
    color: white;
    border-radius: 15px;
    border: 1px solid white;
    -webkit-transition: 1s all;
    transition: 1s all;
}
.nav-right-regist a:hover{
    background: white;
    color: rgb(240, 146, 32);
}
.nav-right-ul .ant-anchor-wrapper{
    background: transparent;
}
.nav-right-ul .ant-anchor-link .ant-anchor-link-title{
    color: white;
}
.nav-right-ul .ant-anchor-ink{
    display: none;
}
.nav-right-ul .ant-anchor{
    display: -webkit-flex;
    display: flex;
}
.nav-right-ul .ant-anchor-link .ant-anchor-link-title-active{
    color: aqua;
}
.aboutMe-page{
    position: absolute;
    width: 100%;
    height: 120%;
    background: #eee;
}
.aboutMe{
    position: absolute;
    width: 400px;
    height: 600px;
    border-radius: 5px 5px 0 0;
    overflow: hidden;
    left: 0;right: 0;top: 0;bottom: 0;margin: auto;
    top: 71px;
    box-shadow: 0.5px 0.5px 2px 0.5px;
}
.aboutMe-top{
    width: 100%;
    height: 400px;
    background: #F2753F;
    text-align: center;
}
.aboutMe-top h3{
    color: white;
    font-size: 45px;
    padding-top: 40px;
    padding-bottom: 5px;
}
.aboutMe-top h4{
    color: white;
    padding-top: 15px;
    font-size: 30px;
}
.aboutMe-bottom{
    width: 100%;
    height: 200px;
    padding: 15px;
    padding-top: 30px;
}
.aboutMe-top .ant-avatar-lg{
    width: 160px;
    height: 160px;
}
.aboutMe-bottom p{
    text-indent: 1em;
    font-size: 15px;
}
.myEmail{
    position: absolute;
    bottom: 10px;right: 20px;
    font-size: 16px;
}
